import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { AppStateInterface } from '@store/app/app-state.interface';
import { Store } from '@ngrx/store';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { MessageService } from 'primeng/api';
import { readFileAsBase64 } from '@utils/read-file-as-base-64';
import { Tag } from '@ApiClient/backend/Extra/tag';

@Component({
  selector: 'app-new-post',
  templateUrl: 'new-post.component.html',
  styleUrls: ['new-post.component.scss'],
})
export class NewPostComponent implements OnInit {
  @ViewChild('fileInput') fileInput!: ElementRef;
  isDragover: boolean = false;
  maxLengthDescription: number = 5000;
  filesToSend: string[] = [];
  tags: string[] = [];

  constructor(
    private store: Store<AppStateInterface>,
    private fb: FormBuilder,
    private translate: TranslateService,
    private toast: MessageService,
  ) {}

  async ngOnInit() {
    await Tag.updateLatest();
    await Tag.getAll().then((res) => {
      const data = res.data;
      console.log(data);
    });
  }

  newPostForm = this.fb.group({
    title: new FormControl('', [Validators.required]),
    description: new FormControl('', [Validators.required, Validators.maxLength(this.maxLengthDescription)]),
    tags: new FormControl(''),
  });

  onSubmit() {
    console.log(this.newPostForm.value);
  }

  async handleFilesUpload(event: Event | DragEvent) {
    let fileList: FileList | null = null;
    if (event instanceof Event) {
      const target = event.target as HTMLInputElement;
      fileList = target.files;
    }
    if (event instanceof DragEvent) {
      this.isDragover = false;
      fileList = event.dataTransfer ? event.dataTransfer.files : null;
    }

    if (fileList !== null && fileList.length > 0) {
      const files: File[] = Array.from(fileList);
      const base64Strings: string[] = [];
      for (const file of files) {
        if (!file.type.startsWith('image/')) {
          this.showErrorMessage('ERROR.WRONG_TYPE');
          return;
        } else {
          const fileBase64string = await readFileAsBase64(file);
          base64Strings.push(fileBase64string);
        }
      }
      this.filesToSend = base64Strings;
    }
  }

  clickFileInput() {
    this.filesToSend = [];
    this.fileInput.nativeElement.click();
  }

  private showErrorMessage(key: string): void {
    this.toast.add({
      severity: 'error',
      summary: this.translate.instant('ERROR.TITLE'),
      detail: this.translate.instant(key),
    });
  }

  protected readonly onsubmit = onsubmit;
}
